@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/AlumniSans-Bold.woff2") format("woff2"), url("../fonts/AlumniSans-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  margin: 0 auto;
  max-width: 1160px;
  width: 84.91%;
}
@media (max-width: 1199px) {
  .container {
    width: 88.28%;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 270px;
    width: 90.62%;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  text-decoration: none;
  border: 4px solid #ffffff;
  cursor: pointer;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 700;
  font-style: normal;
  line-height: 14px;
  color: #ffffff;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.header {
  padding: 30px 0 51px;
  background-image: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}

.header__menu {
  display: flex;
}

.header__tel {
  display: block;
  margin-left: auto;
  letter-spacing: 0.03em;
  width: 155px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 26px;
  font-weight: 600;
  font-style: normal;
  line-height: 31px;
  color: #ffffff;
}

.logo {
  display: flex;
  padding-top: 7px;
}

.logo__img {
  display: block;
}

.intro {
  display: grid;
  grid-template-columns: auto 500px;
  padding-right: 200px;
}

.intro__content {
  display: grid;
  grid-template-columns: repeat(2, auto);
  max-width: 397px;
  margin-top: 104px;
  margin-right: 63px;
}

.intro__list {
  position: relative;
  left: 18px;
  margin-top: auto;
  margin-bottom: auto;
  text-align: right;
}

.intro__item {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 21px;
  color: #ffffff;
}

.intro__text span {
  display: block;
  text-align: right;
  margin-right: 55px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 42px;
  font-weight: 600;
  font-style: normal;
  line-height: 58px;
  color: #ffffff;
}

.intro__text h1 {
  margin: 0;
  margin-bottom: 50px;
  text-align: right;
  letter-spacing: 0.05em;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 92px;
  font-weight: 600;
  font-style: normal;
  line-height: 110px;
  color: #ffffff;
}

.intro__button {
  width: 234px;
  padding: 19px 15px;
  margin-top: auto;
  margin-left: auto;
}

.intro__image {
  margin-top: -10px;
}